import $ from 'jquery'

export let slider = {
  /*
   
   next 
   prev 
   go 
   */
  ready(box, config)
  {
    this.scroll = new scrollImg(box, config);
  }
  ,
  next()
  {
    this.scroll.next();
  },
  prev(){
    this.scroll.prev();
  },
  go(ind){
    this.scroll.go_index(parseInt(ind) || 0);
  }
};

function scrollImg(box, config) {
  this.box = $(box);
  this.config = $.extend({}, config || {});
  this.width = this.config.width || this.box.children().eq(0).width();//
  this.size = this.config.size || this.box.children().length;
  this.loop = this.config.loop !== undefined ? (this.config.loop == true ? true : false) : true;//
  this.auto = this.config.auto !== undefined ? (this.config.auto == true ? true : false) : true;//
  this.auto_wait_time = this.config.auto_wait_time || 3000;//
  this.scroll_time = this.config.scroll_time !== undefined ? (this.config.scroll_time > 0 ? this.config.scroll_time : 0) : 300;//
  this.minleft = -this.width * (this.size - 1);//left[]
  this.maxleft = 0;//lfet[]
  this.now_left = 0;//[]
  this.point_x = null;//x
  this.point_y = null;//y
  this.move_left = false;//
  this.index = 0;
  this.busy = false;
  this.timer;
  this.init();
}
$.extend(scrollImg.prototype, {
  init: function () {
    this.bind_event();
    this.init_loop();
    this.auto_scroll();
  },
  bind_event: function () {
    var self = this;
    self.box.bind('touchstart', function (e) {
      var t = e.touches ? e.touches : e.originalEvent.targetTouches;
      if (t.length == 1 && !self.busy) {
        self.point_x = t[0].screenX;
        self.point_y = t[0].screenY;
      }
    }).bind('touchmove', function (e) {
      var t = e.touches ? e.touches : e.originalEvent.targetTouches;
      console.log('touthes:'+t.length);
      if (t.length == 1 && !self.busy) {
        return self.move(t[0].screenX, t[0].screenY);//touch
      }
    }).bind('touchend', function (e) {
      !self.busy && self.move_end();
    });
  },
  /*
   ,,
   
   
   */
  init_loop: function () {
    if (this.box.children().length > 1 && this.box.children().length == this.size && this.loop) {//size
      this.now_left = -this.width;//
      this.minleft = -this.width * this.size;//left
      this.maxleft = -this.width;
      this.box.prepend(this.box.children().eq(this.size - 1).clone()).append(this.box.children().eq(1).clone()).css(this.get_style(2));
      this.box.css('width', this.width * (this.size + 2));
    } else {
      this.loop = false;
      this.box.css('width', this.width * this.size);
    }
  },
  auto_scroll: function () {//
    var self = this;
    if (!self.auto)return;
    clearTimeout(self.timer);
    self.timer = setTimeout(function () {
      self.go_index(self.index + 1);
    }, self.auto_wait_time);
  },
  go_index: function (ind) {//
    var self = this;
    if (self.busy)return;
    clearTimeout(self.timer);
    self.busy = true;
    if (self.loop) {//
      ind = ind < 0 ? -1 : ind;
      ind = ind > self.size ? self.size : ind;
    } else {
      ind = ind < 0 ? 0 : ind;
      ind = ind >= self.size ? (self.size - 1) : ind;
    }
    if (!self.loop && (self.now_left == -(self.width * ind))) {
      self.complete(ind);
    } else if (self.loop && (self.now_left == -self.width * (ind + 1))) {
      self.complete(ind);
    } else {
      if (ind == -1 || ind == self.size) {//
        self.index = ind == -1 ? (self.size - 1) : 0;
        self.now_left = ind == -1 ? 0 : -self.width * (self.size + 1);
      } else {
        self.index = ind;
        self.now_left = -(self.width * (self.index + (self.loop ? 1 : 0)));
      }
      self.box.css(this.get_style(1));
      setTimeout(function () {
        self.complete(ind);
      }, self.scroll_time);
    }
  },
  complete: function (ind) {//
    var self = this;
    self.busy = false;
    self.config.callback && self.config.callback(self.index);
    if (ind == -1) {
      self.now_left = self.minleft;
    } else if (ind == self.size) {
      self.now_left = self.maxleft;
    }
    self.box.css(this.get_style(2));
    self.auto_scroll();
  },
  next: function () {//
    if (!this.busy) {
      this.go_index(this.index + 1);
    }
  },
  prev: function () {//
    if (!this.busy) {
      this.go_index(this.index - 1);
    }
  },
  move: function (point_x, point_y) {//
    var changeX = point_x - (this.point_x === null ? point_x : this.point_x),
      changeY = point_y - (this.point_y === null ? point_y : this.point_y),
      marginleft = this.now_left, return_value = false,
      sin = changeY / Math.sqrt(changeX * changeX + changeY * changeY);
    this.now_left = marginleft + changeX;
    this.move_left = changeX < 0;
    if (sin > Math.sin(Math.PI / 3) || sin < -Math.sin(Math.PI / 3)) {//PI/3  -- 2PI/3
      return_value = true;//
    }
    this.point_x = point_x;
    this.point_y = point_y;
    this.box.css(this.get_style(2));
    return return_value;
  },
  move_end: function () {
    var changeX = this.now_left % this.width, ind;
    if (this.now_left < this.minleft) {//
      ind = this.index + 1;
    } else if (this.now_left > this.maxleft) {//
      ind = this.index - 1;
    } else if (changeX != 0) {
      if (this.move_left) {//
        ind = this.index + 1;
      } else {//
        ind = this.index - 1;
      }
    } else {
      ind = this.index;
    }
    this.point_x = this.point_y = null;
    this.go_index(ind);
  },
  /*
   
   @int fig : 1  2  
   */
  get_style: function (fig) {
    var x = this.now_left,
      time = fig == 1 ? this.scroll_time : 0;
    return {
      '-webkit-transition': '-webkit-transform ' + time + 'ms',
      '-webkit-transform': 'translate3d(' + x + 'px,0,0)',
      '-webkit-backface-visibility': 'hidden',
      'transition': 'transform ' + time + 'ms',
      'transform': 'translate3d(' + x + 'px,0,0)'
    };
  }
});



